@import "src/constants";

.properties {
  table {
    /* equal sized cols */
    table-layout: fixed;

    /* drop the gap between rows */
    border-collapse: collapse;
  }

  &.expanded {
    width: 100%;

    table, tbody {
      width: 100%;
    }
  }


  td {
    width: 50%;
    padding: $unit;
  }
  tr {
    border: solid;

    /* add hz lines between rows,
       including above
       the first and below the last
    */
    border-width: 0px 0px 0.25px 0px;
    &:first-child {
      border-width: 0.25px 0px 0.25px 0px;
    }


    &:nth-child(odd) {
      background-color: darken($neutral, 2);
    }

  }
}

.property-wrapper {
  .property-key {
    font-weight: bold;
  }

  .property-value {
    margin-left: 0.5 * $unit;
  }

  &.private {
    .property-value {
      color: transparent;
      text-shadow: 0 0 5px rgba(0,0,0,0.5);
    }
  }
}
